@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;900&display=swap");
.particles {
  z-index: 0;
}

.main {
  z-index: 10;
  background-color: #222222;
  color: #fff;
  min-height: 80vh;
  width: 100%;
  min-width: 370px;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  font-size: 20px;
  font-family: "Poppins", sans-serif;
  font-weight: bold;

  h1 {
    font-size: 80px;
    font-weight: 900;
    text-align: center;
  }

  &__content {
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    width: 90%;
    min-height: 200px;
  }

  &__content_item {
    text-indent: 20px;
    width: 40%;
    height: 70%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
    position: relative;
  }
  &__content_item p {
    text-align: center;
    width: 80%;
    padding: 5px;
    font-weight: 500;
    line-height: 120%;
    font-size: 20px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      display: block;
      width: 40px;
      height: 3px;
      background-color: yellow;
      top: 50% - 6%;
      left: -9%;
    }
    &::before {
      content: "";
      position: absolute;
      display: block;
      width: 40px;
      height: 3px;
      background-color: yellow;
      top: 50% - 6%;
      right: -9%;
    }
  }

  &__content_photo {
    max-height: 250px;
    min-height: 250px;
    max-width: 600px;
    position: relative;
    z-index: 2;

    &::after {
      content: "";
      display: block;
      position: absolute;
      background-color: #222222;
      border: 5px solid yellow;

      min-height: 230px;
      width: 250px;
      z-index: -2;
      top: 16%;
      right: 24%;
    }
  }
  &__content_img {
    width: 250px;
    height: 250px;
    object-fit: cover;
  }
}

@media (max-width: 1200px) {
  .main {
    max-width: 970px;
    p {
      font-size: 18px;
      &::after {
        width: 30px;
      }
      &::before {
        width: 30px;
      }
    }
    h1 {
      font-size: 70px;
    }
  }
}
@media (max-width: 992px) {
  .main {
    max-width: 750px;
    p {
      font-size: 16px;
      &::after {
        width: 25px;
      }
      &::before {
        width: 25px;
      }
    }
    h1 {
      font-size: 60px;
    }
  }
}
@media (max-width: 900px) {
  .main {
    h1 {
      font-size: 50px;
    }
  }
  .main__content {
    flex-direction: column-reverse;
    margin-top: 20px;
    width: 100%;
  }
  .main__content_photo {
    margin: 40px 0;
  }
  .main__content_item {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .main {
    min-width: 370px;
    max-width: 100%;
    h1 {
      font-size: 35px;
      margin-top: 30px;
    }
    p {
      &::after {
        width: 0;
      }
      &::before {
        width: 0;
      }
    }
    &__content {
      flex-direction: column-reverse;
      margin-top: 20px;
      width: 100%;
    }
    &__content {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &__content_item {
      width: 100%;
      font-size: 16px;
    }
    &__content_photo {
      margin: 40px 0;
      max-height: 250px;
      min-height: 250px;
      max-width: 600px;
      position: relative;
      z-index: 2;
      img {
        border-radius: 5%;
      }
    }
  }
}
